@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-family: Arial, sans-serif; font-size: 18px; line-height: 1.2; }

@font-face { font-family: "Proximanova Regular"; src: url("fonts/proximanova-regular.eot"); src: url("fonts/proximanova-regular.eot?#iefix") format("embedded-opentype"), url("fonts/proximanova-regular.woff2") format("woff2"), url("fonts/proximanova-regular.woff") format("woff"), url("fonts/proximanova-regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "PfSqu"; src: url("fonts/pf_square_sans_pro_bold.eot"); src: url("fonts/pf_square_sans_pro_bold.eot?#iefix") format("embedded-opentype"), url("fonts/pf_square_sans_pro_bold.woff2") format("woff2"), url("fonts/pf_square_sans_pro_bold.woff") format("woff"), url("fonts/pf_square_sans_pro_bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.out { background-image: linear-gradient(to top, black 0%, black 13%, #ad1285 50%, black 86%, black 100%); }

img { max-width: 100%; height: auto; }

.input-text { border-radius: 3px; background-color: #FFF; height: 34px; line-height: 32px; font-family: "Proximanova Regular", sans-serif; font-size: 11.21px; color: #959da2; padding: 6px 15px; width: 100%; }

.btn { height: 34px; line-height: 33px; display: inline-block; font-family: "Proximanova Regular", sans-serif; font-size: 11.21px; color: #fff; cursor: pointer; position: relative; transition: all 0.3s; }
.btn:active { top: 1px; }
.btn:hover { background-color: #80075a; }

.love { background: url(../img/love.png) no-repeat center; height: 879px; background-size: cover; text-align: center; }
@media only screen and (max-width: 1024px) { .love { background: none; } }
.love .love__in { max-width: 680px; margin: auto; padding: 0 20px; }
.love .love__tpic { padding-top: 170px; }
.love .love__mpic { margin-top: -23px; }
.love .love__bpic { margin-top: 30px; }
.love img { display: inline-block; }
.love .go-in { margin-top: 41px; }

.go-in { padding: 18px; background-color: #b7176e; border-radius: 3px; margin: auto; position: relative; }
.go-in .row { font-size: 0; }
.go-in [class^="col"], .go-in [class*="col"] { display: inline-block; vertical-align: top; }
.go-in .col1 { padding-right: 18px; width: 78%; }
@media only screen and (max-width: 1024px) { .go-in .col1 { width: 65%; } }
@media only screen and (max-width: 450px) { .go-in .col1 { width: 100%; padding-right: 0; margin-bottom: 10px; } }
.go-in .col2 { width: 22%; }
@media only screen and (max-width: 1024px) { .go-in .col2 { width: 35%; } }
@media only screen and (max-width: 450px) { .go-in .col2 { width: 100%; } }
.go-in .btn { text-transform: uppercase; background-color: #370327; width: 100%; }


